.pack {
    &__label {
        display: block;
        margin-block-start: var(--margin__medium);
        margin-block-end: var(--margin__small);

        .icon {
            margin-right: calc(0.2em + 0.5rem);
        }
    }

    &__item__category-heading {
        font-weight: 600;
        text-transform: uppercase;
    }

    &__list {
        list-style: none;
        max-width: 35em;
        margin: 0;
        padding: 0;
        display: block;
        margin-block-start: var(--margin__medium);
        margin-block-end: var(--margin__small);

        &__bar {
            position: absolute !important;
            height: 100%;
            right: 0;
            top: 0;
            z-index: 1 !important;
            opacity: .5;
        }

        &-item {
            display: grid;
            grid-template-columns: 2em 1fr auto;
            grid-gap: 0 0.5rem;
            grid-template-rows: auto;
            grid-template-areas:
                "left middle right"
                "bottom-left bottom bottom-right";
            margin-bottom: 0.5em;

            &.-overview {
                grid-template-columns: 2em 1fr auto;
                grid-template-areas:
                    "left middle right"
                    "bottom-left bottom bottom-right";
                position: relative;
                background: hsl(0deg 0% 70% / 10%);
                margin-bottom: 0.1rem;

                .icon {
                    margin-left: .3rem;
                }

                > span {
                    position: relative;
                    z-index: 2;
                }
            }

            &__left {
                grid-area: left;
            }

            &__middle {
                grid-area: middle;
            }

            &__right {
                grid-area: right;
                text-align: right;
            }

            &__bottom-left {
                grid-area: bottom-left;
                font-size: var(--font__size-base);
            }

            &__bottom {
                grid-area: bottom;
                font-size: var(--font__size-base);
            }

            &__bottom-right {
                grid-area: bottom-right;
                font-size: var(--font__size-base);
                text-align: right;
            }
        }
    }

    &__list-button {
        position: absolute;
        z-index: 1;
        bottom: 0;
    }
}

// Features with JavaScript enabled
.js {
    .pack {
        &__list-container {
            overflow: hidden;

            &.-collapsed {
                position: relative;
                max-height: 20rem;

            }

            &.-collapsed:after {
                content: "";
                position: absolute;
                width: 100%;
                height: 100%;
                background: var(--color__background__gradient);
                top: 0;
                left: 0;
            }

            > :first-child {
                margin-top: 0;
            }
        }
    }
}
